*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: $ff-robotto;
  color: $color-default-black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  //      
  text-rendering: optimizeLegibility;
  //       
  // https://transfonter.org/ -   https://prnt.sc/12rnt6g   
}

body {
  width: 100%;
  height: 100%;

  background-color: $color-default-white;

  &.open {
    overflow: hidden;
  }
}

a {
  color: $color-default-black;
  text-decoration: none;
  transition: all $trans-default;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
//         \ -
//          
// rgba  ,    hex  
//      box-shadow    -
//     `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {

  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}

h1,
h2,
h3,
h2,
p {
  margin-top: 0;
}

h2 {
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 9px;
  border-bottom: 6px solid $color-gold;
  margin-bottom: 40px;

  @include vp-1023 {
    font-size: 30px;
  }

  @include vp-639 {
    font-size: 26px;
  }
}

h3,
h4 {
  font-weight: 500;
}

p {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }
}

[data-js-focus-visible] {
  .header {
    top: 32px;
  }
  .wrapper {

  }
}
