.filter {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  margin-bottom: 60px;
  text-align: center;
  font-size: 18px;

  ul {
    display: flex;
    border: 1px solid $color-default-black;

    @include vp-767 {
      flex-wrap: wrap;
    }
  }

  li {
    border-right: 1px solid $color-default-black;
    margin-bottom: 0;

    @include vp-767 {
      width: 50%;
    }

    &:last-child {
      border-right: none;
    }

    &:nth-child(even) {
      @include vp-767 {
        border-right: none;
      }
    }

    &:nth-child(n + 3) {
      @include vp-767 {
        border-top: 1px solid $color-default-black;
      }
    }
  }

  a {
    display: block;
    padding: 15px 50px;

    @include vp-1023 {
      font-size: 16px;
      padding: 10px 25px;
    }

    @include hover-focus {
      color: $color-dark-grey;
      background-color: $color-gold;
    }
  }
}
